  margin: 0;
  padding: 0;
  border: none;
}
body {
  /*font: 20px '华文细黑','微软雅黑', '宋体', Tahoma, Arial, sans-serif;*/
  color: #555;
  width: 100%;
  height: 100%;
}
em, i {
  font-style: normal;
}
a {
  text-decoration: none;
  color: #000;
  /*font-family:'微软雅黑', '宋体', Microsoft YaHei, Tahoma, Arial, sans-serif;*/
